#page404 {
	background-image: url('../img/404.webp');
	background-size: cover;
	background-repeat: no-repeat;
	min-height: 100vh;
	min-width: 100vw;
	font-family: "Raleway", Arial, Helvetica, sans;
	color: #C05A5C;
	color: rgba(255, 255, 255, 0.87);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
	h1 {
		font-size: 60px;
		font-weight: bold;
		color: #fff;
		text-shadow: 0px 1px 2px #000;
	}
}
.home-content {
	padding: 5%;
	text-align: center;
	background-color: rgba(0,0,0,0.5);
	>h1 {
		>span {
			color: #C05A5C;
			font-size: 5rem;
			font-weight: 800;
			letter-spacing: 0.5rem;
		}
	}
}
.btn-outline {
	border: 2px solid #fff;
	color: #fff;
	padding: 12px 40px;
	border-radius: 25px;
	margin-top: 25px;
	display: inline-block;
	font-weight: 600;
	text-decoration: none;
	text-transform: uppercase;
	font-weight: 400;
	font-size: 1rem;
	&:hover {
		color: black;
		background: #fff;
		text-decoration: none;
		text-transform: uppercase;
		font-weight: 400;
		font-size: 1rem;
		font-weight: 600;
		opacity: 0.8;
		cursor: pointer;
	}
}
.opacity404 {
	opacity: 0.9;
}
@media (max-width: 1024px) {
	.home-content {
		>h1 {
			>span {
				color: #C05A5C;
				font-size: 5rem;
				font-weight: 800;
				letter-spacing: 0.5rem;
			}
		}
	}
}
